import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box, Typography } from "@rp/react/web/mui/core"

function HubHeadCell({ label = "" }: { label: string }) {
  const { isMobile } = useDeviceInfo()
  return (
    <Box sx={{ display: "flex", alignItems: "center" }}>
      {isMobile ? null : (
        <Box
          sx={{
            userSelect: "none",
            display: "flex",
            alignItems: "center",
            gap: 1,
          }}
        >
          <Typography sx={{ fontSize: 11, fontWeight: 700 }}>{label}</Typography>
        </Box>
      )}
    </Box>
  )
}

export { HubHeadCell }
